function SubmissionModal({ type, articleId }: { type: 'comment' | 'story', articleId: string }) {
    const { toast } = useToast();
    const [content, setContent] = useState('');
    const [image, setImage] = useState<File | null>(null);
    const [imagePreview, setImagePreview] = useState<string | null>(null);
    const [open, setOpen] = useState(false);

    const isComment = type === 'comment';
    const charLimit = isComment ? 300 : 500;
    const title = isComment ? 'Add a Comment' : 'Add Your Story';
    const description = isComment ? 'Share your thoughts on this article.' : 'Share a follow-up story with the community.';
    const buttonIcon = isComment ? <MessageSquare className="h-5 w-5" /> : <Pencil className="h-5 w-5" />;
    
    const handleImageChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        if (e.target.files && e.target.files[0]) {
            const file = e.target.files[0];
            setImage(file);
            setImagePreview(URL.createObjectURL(file));
        }
    };

    const handleRemoveImage = () => {
        setImage(null);
        setImagePreview(null);
    }

    const handleSubmit = (e: React.FormEvent) => {
        e.preventDefault();
        if (!content.trim()) {
            toast({
                title: 'Content cannot be empty.',
                variant: 'destructive',
            });
            return;
        }
        // Firestore logic to save content would go here
        console.log({
            articleId,
            type,
            content,
            image,
        });
        toast({
            title: 'Success!',
            description: `Your ${type} has been submitted.`,
        });
        setContent('');
        setImage(null);
        setImagePreview(null);
        setOpen(false);
    };


    return (
        <Dialog open={open} onOpenChange={setOpen}>
            <DialogTrigger asChild>
                <Button className="mt-6 w-full" variant="outline">
                    {buttonIcon}
                    <span>{title}</span>
                </Button>
            </DialogTrigger>
            <DialogContent className="sm:max-w-[425px]">
                <DialogHeader>
                    <DialogTitle>{title}</DialogTitle>
                    <DialogDescription>{description}</DialogDescription>
                </DialogHeader>
                <form onSubmit={handleSubmit} className="space-y-4">
                    <div className="grid w-full gap-1.5">
                         <Label htmlFor="content">{isComment ? 'Comment' : 'Story'}</Label>
                         <Textarea
                            value={content}
                            onChange={(e) => setContent(e.target.value)}
                            placeholder={`Type your ${type} here...`}
                            id="content"
                            maxLength={charLimit}
                            className="min-h-[120px]"
                        />
                        <p className="text-sm text-muted-foreground text-right">{content.length} / {charLimit}</p>
                    </div>

                     {imagePreview && (
                        <div className="relative w-full h-32">
                            <Image src={imagePreview} alt="Image preview" fill className="rounded-md object-cover" />
                            <Button
                                type="button"
                                variant="destructive"
                                size="icon"
                                className="absolute -top-2 -right-2 h-6 w-6 rounded-full"
                                onClick={handleRemoveImage}
                            >
                                <X className="h-4 w-4" />
                            </Button>
                        </div>
                    )}
                    
                    <div className="flex justify-between items-center">
                        <Button asChild variant="ghost" size="icon" className="cursor-pointer">
                            <label htmlFor="image-upload">
                                <Paperclip className="h-5 w-5" />
                                <input id="image-upload" type="file" accept="image/*" className="hidden" onChange={handleImageChange} />
                            </label>
                        </Button>
                        <DialogFooter>
                            <DialogClose asChild>
                               <Button type="button" variant="secondary">Cancel</Button>
                            </DialogClose>
                            <Button type="submit">
                                <Send className="mr-2 h-4 w-4" />
                                Post
                            </Button>
                        </DialogFooter>
                    </div>
                </form>
            </DialogContent>
        </Dialog>
    )
}
